<template>
  <div class="pay-wrap">
    <div class="pay-info">
      <div class="pay-icon">
        <img :src="result ? '/assets/image/common/success.png' : '/assets/image/common/error.png'">
      </div>
      <div class="pay-message" :class="{error: !result}">支付{{result ? '成功' : '失败'}}</div>
      <div class="btn-pay" @click="goback()">返回首页</div>
    </div>
  </div>
</template>
<script>

export default {
  data() {
    return {
      result: false
    }
  },
  components: {
  },
  created() {
    this.result = this.getUrlParam('r') === '1'
    window.title = '支付' + (this.result ? '成功' : '失败')
  },
  methods: {
    getUrlParam(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
      var r = window.location.search.substr(1).match(reg);  //匹配目标参数
      if (r != null) return decodeURI(r[2]); return null; //返回参数值
    },
    goback() {
      location.href = '/activity/class/detail?id=3280'
    }
  }
}
</script>
<style lang="less">
.pay-wrap {
  display: flex;
  height: 100vh;
  justify-content: center;
  text-align: center;
  padding: 0rem 0.88rem;

}

.pay-info {
  width: 100%;
  padding: 1.5rem 0rem;
}

.pay-icon {
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 auto;

  img {
    width: 100%;
    height: 100%;
  }
}

.pay-message {
   font-size: 0.32rem;
    color: #07C160;
    padding: 0.32rem 0.44rem;

    &.error {
      color: #FA5151;
    }
}


  .btn-pay {
    background-color: #22292C;
    font-size: 0.32rem;
    color: white;
    padding: 0.28rem 0.44rem;
  }
</style>



